import React from 'react'
import M from './Post.module.css';

const Post = (props) => {
    return (
        <div className={M.post}>
            <img src="data:image/jpeg;base64," />
            {props.message}
            <div className={M.like}>
                {props.countLike}
            </div>

        </div>)
}
export default Post;